*, *::before, *::after {
  box-sizing: border-box;
}

html {
  font: 62.5% / 1.15 sans-serif;
}

h1, h2 {
  margin-bottom: 0;
}

ul {
  list-style: none;
  padding: 0;
}

button {
  border: none;
  margin: 0;
  padding: 0;
  width: auto;
  overflow: visible;
  background: transparent;
  color: inherit;
  font: inherit;
  line-height: normal;
}

button, input, optgroup, select, textarea {
  font-family: inherit;
  font-size: 100%;
  line-height: 1.15;
  margin: 0;
}

button, input {
  overflow: visible;
}

input[type="text"] {
  border-radius: 0;
}

body {
  width: 100%;
  max-width: 68rem;
  margin: 0 auto;
  font: 1.6rem/1.25 Arial, sans-serif;
  background: #f5f5f5;
  color: #4d4d4d;
}

@media screen and (min-width: 620px) {
  body {
    font-size: 1.9rem;
    line-height: 1.31579;
  }
}

.form-group > input[type="text"] {
  display: inline-block;
  margin-top: 0.4rem;
}

.btn {
  padding: 0.8rem 1rem 0.7rem;
  border: 0.2rem solid #4d4d4d;
  cursor: pointer;
  text-transform: capitalize;
}

.btn.toggle-btn {
  border-width: 1px;
  border-color: #d3d3d3;
}

.btn.toggle-btn[aria-pressed="true"] {
  text-decoration: underline;
  border-color: #4d4d4d;
}

.btn_danger {
  color: #fff;
  background: #f5222d;
  border-color: #cf1322;
}

.btn_primary {
  color: #fff;
  background: #1890ff;
  border-color: #1890ff;
}

.btn-group {
  display: flex;
  justify-content: space-between;
}

.btn-group > * {
  flex: 1 1 49%
}

.btn-group > * + * {
  margin-left: 0.8rem;
}

.label-wrapper {
  margin: 0;
  flex: 0 0 100%;
  text-align: center;
}

[class*="stack"] > * {
  margin-top: 0;
  margin-bottom: 0;
}

.stack-small > * + * {
  margin-top: 1.25rem;
}

.stack-large > * + * {
  margin-top: 2.5rem;
}

@media screen and (min-width: 550px) {
  .stack-small > * + * {
    margin-top: 1.4rem;
  }

  .stack-large > * + * {
    margin-top: 2.8rem;
  }
}

.stack-exception {
  margin-top: 1.2rem;
}

.todoapp {
  background: #fff;
  margin: 2rem 0 4rem 0;
  padding: 1rem;
  position: relative;
  box-shadow: 0 2px 4px 0 rgba(0, 0, 0, 0.2), 0 2.5rem 5rem 0 rgba(0, 0, 0, 0.1);
}

@media screen and (min-width: 550px) {
  .todoapp {
    padding: 4rem;
  }
}

.todoapp > * {
  max-width: 50rem;
  margin-left: auto;
  margin-right: auto;
}

.todoapp > form {
  max-width: 100%;
}

.todoapp > h1 {
  display: block;
  max-width: 100%;
  text-align: center;
  margin: 0 0 1rem;
}

.label_lg {
  line-height: 1.01567;
  font-weight: 300;
  padding: 0.8rem;
  margin-bottom: 1rem;
  text-align: center;
}

.input_lg {
  padding: 2rem;
  border: 2px solid #d9d9d9;
}

.input_lg:hover {
  border-color: #40a9ff;
}

.input_lg:focus {
  border-color: #40a9ff;
  outline: 0;
  box-shadow: 0 0 0 2px rgba(24, 144, 255, .2);
}

[class*="_lg"] {
  display: inline-block;
  width: 100%;
  font-size: 1.9rem;
}

[class*="_lg"]:not(:last-child) {
  margin-bottom: 1rem;
}

@media screen and (min-width: 620px) {
  [class*="_lg"] {
    font-size: 2.4rem;
  }
}

.filters {
  width: 100%;
  margin: 1rem auto 0;
}

.todo {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
}

.todo > * {
  flex: 0 0 100%;
}

.todo-text {
  width: 100%;
  min-height: 4.4rem;
  padding: 0.4rem 0.8rem;
  border: 2px solid #565656;
}

.todo-text:focus {
  box-shadow: inset 0 0 0 2px;
}

.todo-label {
  margin-left: 1rem;
}

.c-cb {
  box-sizing: border-box;
  font-family: Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  font-weight: 400;
  font-size: 1.6rem;
  line-height: 1.25;
  display: block;
  position: relative;
  min-height: 44px;
  padding-left: 16px;
  clear: left;
}

.c-cb > label:before, .c-cb > input[type="checkbox"] {
  box-sizing: border-box;
  top: -2px;
  left: -2px;
  width: 32px;
  height: 32px;
}

.c-cb > input[type="checkbox"] {
  -webkit-font-smoothing: antialiased;
  cursor: pointer;
  position: absolute;
  z-index: 1;
  margin: 0;
  opacity: 0;
}

.c-cb > label {
  font-size: inherit;
  font-family: inherit;
  line-height: inherit;
  display: inline-block;
  margin-bottom: 0;
  padding: 8px 15px 5px;
  cursor: pointer;
  touch-action: manipulation;
}

.c-cb > label::before {
  content: "";
  position: absolute;
  border: 2px solid currentColor;
  background: transparent;
}

.c-cb > input[type="checkbox"]:focus + label::before {
  border-color: #40a9ff;
  box-shadow: 0 0 0 2px rgba(24,144,255,.2);
}

.c-cb > label::after {
  box-sizing: content-box;
  content: "";
  position: absolute;
  top: 6px;
  left: 4px;
  width: 16px;
  height: 8px;
  transform: rotate(-45deg);
  border: solid #228bec;
  border-width: 0 0 5px 5px;
  border-top-color: transparent;
  opacity: 0;
  background: transparent;
}

.c-cb > input[type="checkbox"]:checked + label::after {
  opacity: 1;
}